        center: new kakao.maps.LatLng(this.xy[1], this.xy[0]),
        level: 4, // ÏßÄÎèÑÏùò ÌôïÎåÄ Î†àÎ≤®
      };

    // ÏßÄÎèÑÎ•º ÌëúÏãúÌï† divÏôÄ  ÏßÄÎèÑ ÏòµÏÖòÏúºÎ°ú  ÏßÄÎèÑÎ•º ÏÉùÏÑ±Ìï©ÎãàÎã§
    let map = new kakao.maps.Map(mapContainer, mapOption);
    // console.log(map);

    let positions = [
      {
        title: "Ïò§Î¶Ñ",
        latlng: new kakao.maps.LatLng(this.xy[1], this.xy[0]),
      },
    ];
    // for (let i = 0; i < this.result.length; i++) {
    //   positions[i] = {
    //     title: this.result[i].oleumKname,
    //     latlng: new kakao.maps.LatLng(this.result[i].y, this.result[i].x),
    //   };
    // }
    // console.log(positions);

    // ÎßàÏª§ Ïù¥ÎØ∏ÏßÄÏùò Ïù¥ÎØ∏ÏßÄ Ï£ºÏÜåÏûÖÎãàÎã§
    let imageSrc =
