<template>
  <div>
    <div ref="map" style="width: 100%; height: 94.9vh"></div>
  </div>
</template>

<script>
export default {
  props: {
    result: Array,
  },
  methods: {},
  mounted() {
    const { kakao } = window;
    let mapContainer = this.$refs.map, // ÏßÄÎèÑÎ•º ÌëúÏãúÌï† div
      mapOption = {
        center: new kakao.maps.LatLng(33.362, 126.5336), // ÏßÄÎèÑÏùò Ï§ëÏã¨Ï¢åÌëú
        // 33.362,126.5336 ÌïúÎùºÏÇ∞ ÏúÑ,Í≤ΩÎèÑ
        level: 9, // ÏßÄÎèÑÏùò ÌôïÎåÄ Î†àÎ≤®
      };

    // ÏßÄÎèÑÎ•º ÌëúÏãúÌï† divÏôÄ  ÏßÄÎèÑ ÏòµÏÖòÏúºÎ°ú  ÏßÄÎèÑÎ•º ÏÉùÏÑ±Ìï©ÎãàÎã§
    let map = new kakao.maps.Map(mapContainer, mapOption);
    // console.log(map);

    let positions = [];
    for (let i = 0; i < this.result.length; i++) {
      positions[i] = {
        title: this.result[i].oleumKname,
        latlng: new kakao.maps.LatLng(this.result[i].y, this.result[i].x),
      };
    }

    // ÎßàÏª§ Ïù¥ÎØ∏ÏßÄÏùò Ïù¥ÎØ∏ÏßÄ Ï£ºÏÜåÏûÖÎãàÎã§
    let imageSrc =
      "https://t1.daumcdn.net/localimg/localimages/07/mapapidoc/markerStar.png";

    for (let i = 0; i < positions.length; i++) {
      // ÎßàÏª§ Ïù¥ÎØ∏ÏßÄÏùò Ïù¥ÎØ∏ÏßÄ ÌÅ¨Í∏∞ ÏûÖÎãàÎã§
      let imageSize = new kakao.maps.Size(24, 35);

      // ÎßàÏª§ Ïù¥ÎØ∏ÏßÄÎ•º ÏÉùÏÑ±Ìï©ÎãàÎã§
      let markerImage = new kakao.maps.MarkerImage(imageSrc, imageSize);

      // ÎßàÏª§Î•º ÏÉùÏÑ±Ìï©ÎãàÎã§
      let marker = new kakao.maps.Marker({
        map: map, // ÎßàÏª§Î•º ÌëúÏãúÌï† ÏßÄÎèÑ
        position: positions[i].latlng, // ÎßàÏª§Î•º ÌëúÏãúÌï† ÏúÑÏπò
        title: positions[i].title, // ÎßàÏª§Ïùò ÌÉÄÏù¥ÌãÄ, ÎßàÏª§Ïóê ÎßàÏö∞Ïä§Î•º Ïò¨Î¶¨Î©¥ ÌÉÄÏù¥ÌãÄÏù¥ ÌëúÏãúÎê©ÎãàÎã§
        image: markerImage, // ÎßàÏª§ Ïù¥ÎØ∏ÏßÄ
      });
      // console.log(marker);
      marker;
    }
  },
};
</script>

